import { Component, OnInit } from '@angular/core';
import {CourseNavigatorService} from "../services/CourseNavigatorService";

@Component({
  selector: 'app-course-navigator',
  templateUrl: './course-navigator.component.html',
  styleUrls: ['./course-navigator.component.scss']
})
export class CourseNavigatorComponent implements OnInit {

  constructor(private service: CourseNavigatorService) { }

  courses = []
  modules = []
  lessons = []
  topics = []
  widgets = []

  selectedCourse = {}
  selectedModule = {}
  selectedLesson = {}
  selectedTopic = {}

  selectCourse = course => {
    this.selectedCourse = course;
    this.service
      .findModulesForCourse(course.id)
      .then(modules => this.modules = modules)
  }


  selectModule = module => {
    this.selectedModule = module;
    this.service
      .findLessonsForModule(module.id)
      .then(lessons => this.lessons = lessons)
  }

  selectLesson = lesson => {
    this.selectedLesson = lesson;
    this.service
      .findTopicsForLesson(lesson.id)
      .then(topics => this.topics = topics)
  }

  selectTopic = topic => {
    this.selectedTopic = topic;
    this.service
      .findWidgetsForTopic(topic.id)
      .then(widgets => this.widgets = widgets)
  }



  ngOnInit() {
    this.service
      .findAllCourses()
      .then(courses => this.courses = courses)
  }

}
