
// const API_URL = 'https://glacial-plateau-74223.herokuapp.com/api/';
const API_URL = 'http://localhost:8080/api/';
export class CourseNavigatorService {

  findAllCourses = () =>
    fetch
    ('http://localhost:8080/api/allcourse')
      .then(response => response.json());


  findModulesForCourse = courseId =>
    fetch
    (`http://localhost:8080/api/course/${courseId}/module`)
      .then(response => response.json());


  findLessonsForModule = moduleId =>
    fetch
    (`http://localhost:8080/api/module/${moduleId}/lesson`)
      .then(response => response.json());

  findTopicsForLesson = lessonId =>
    fetch
    (`http://localhost:8080/api/lesson/${lessonId}/topic`)
      .then(response => response.json());

  findWidgetsForTopic= topicId =>
    fetch
    (`http://localhost:8080/api/topic/${topicId}/widget`)
      .then(response => response.json());
}


